import React from 'react'
import { Link } from 'react-router-dom'
import './Home.css'


function Home({ cheese, milk, pineapple, tomato }) {

    const total = tomato + cheese + milk + pineapple

    const link_home = {
        color: "#fff",
        backgroundColor: '#2b8379',
        textDecoration: 'none',
        borderRadius: '3px',
        display: "inline-block",
        cursor: "pointer",
        fontSize: "14px",
        fontWeight: "600",
        padding: "13px 40px",
        border: "0",
        transition: "background-color .2s,color .2s"
    }

    function MouseOver(event) {
        event.target.style.background = "#19a596";
      }

      function MouseOut(event){
        event.target.style.background="#2b8379";
      }  


    return (
        <div>
            <nav className="navbar">
            <Link className="nav-brand active" to="/"> OnlineM</Link>

                <ul>
                    <li className='nav-item'> <a href="#" className="product-active">Home </a></li>
                    <li className='nav-item'> <Link to="/about">About Us </Link> </li>
                    <li className='nav-item'> <Link to="/product">Products </Link> </li>
                    <li>
                        <Link to="/checkout" className="nav-item nav-cart btn-accent btn"> Cart {total} </Link>
                    </li>
                </ul>
            </nav>

            <div className="container">
                <div className="home-layout">
                    <div>
                        <h1>Online shopping simplified</h1>
                        <p>
                            Order your groceries from <em>OnlineM</em> with our easy to use app, and get your products delivered straight to your doorstep.
                    </p>
                        <div className="link-home"><Link to="/product" style={link_home } onMouseOver={MouseOver} onMouseOut={MouseOut}>Start shopping </Link> </div>
                    </div>
                    <img src="https://res.cloudinary.com/dbfn5lnvx/image/upload/q_auto,w_700/v1607770215/react-tutorial/supermarket/home.jpg" width="350" height="240" className="rounded home-image" alt="" />
                </div>
            </div>

        </div>
    )
}

export default Home
