import React, {useState, useEffect} from 'react'
import './App.css';
import Product from './components/Product'
import CheckOut from './components/CheckOut'
import Home from './components/Home'
import About from './components/About'

import { Route, HashRouter as Router, Switch } from 'react-router-dom'


function App() {
  const [cheese, setCheese] = useState(0)
  const [milk, setMilk] = useState(0)
  const [tomato, setTomato] = useState(0)
  const [pineapple, setPineapple] = useState(0)

  useEffect (()=>{
    const data = localStorage.getItem('cheese');
    if(data) {
        setCheese(JSON.parse(data))
    }
}, [])

useEffect(()=>{
    localStorage.setItem( 'cheese', JSON.stringify(cheese))
})




useEffect (()=>{
    const data = localStorage.getItem('milk');
    if(data) {
        setMilk(JSON.parse(data))
    }
}, [])

useEffect(()=>{
    localStorage.setItem( 'milk', JSON.stringify(milk))
})




useEffect (()=>{
    const data = localStorage.getItem('tomato');
    if(data) {
        setTomato(JSON.parse(data))
    }
}, [])

useEffect(()=>{
    localStorage.setItem( 'tomato', JSON.stringify(tomato))
})



useEffect (()=>{
    const data = localStorage.getItem('pineapple');
    if(data) {
        setPineapple(JSON.parse(data))
    }
}, [])

useEffect(()=>{
    localStorage.setItem( 'pineapple', JSON.stringify(pineapple))
})

  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path='/' exact  render={ props=> <Home cheese={cheese}  milk = {milk}   tomato = {tomato}  pineapple ={pineapple} />} />
          <Route path='/about'   render={ props=> <About cheese={cheese}  milk = {milk}   tomato = {tomato}  pineapple ={pineapple} />} />
          <Route path='/product'  render={ props=> <Product cheese={cheese} setCheese = {setCheese} milk = {milk} setMilk ={setMilk}  tomato = {tomato} setTomato={setTomato} pineapple ={pineapple} setPineapple={setPineapple}/>} />
          <Route path='/checkout'  render={ props=> <CheckOut cheese={cheese} setCheese = {setCheese} milk = {milk}  setMilk ={setMilk}  tomato = {tomato} setTomato={setTomato} pineapple ={pineapple} setPineapple={setPineapple} />} />
         
        </Switch>
      </div>
    </Router>
  );
}

export default App;
